.todoGroup {
    padding: 4px;
}
.todoGroup > li:hover {
    background:#f5f5f5;
}
.ListItem {
    position: relative;
    padding: 6px;
    color: #34373d; 
    font-size: 15px;
    font-weight: 400;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    
}
.ListItem .groupdeleted {
    display: none;
}
.active .groupdeleted {
    display: block;
    position: absolute;
    right: 7px;
    color: #73be66;
    cursor: pointer;
    pointer-events: auto;
}
.ListItem .show {
    display: block;
}
.ListItem .icon {
    font-size: 26px;
    color: #3e69e4;
    pointer-events: none;
}
.ListItem .listname {
    padding-right: 15px;
    pointer-events: none;
}
.active {
    background: #ededed;
    border-radius: 3px;
}


